.trademark-details {
  background-color: #f3f3f3 !important;
  border: 1px solid #dddddd !important;
  padding: 20px;
  margin-top: 90px;
  margin-bottom: 30px;
  font-size: 14px;
  font-family: 'Open Sans', sans-serif important;
  line-height: 18px;
}
.cart-info {
  border-top: 1px dashed #dddddd;
  margin-top: 10px;
  padding-top: 20px;
}
.addToCartTrademark {
  background: #bce27f !important;
  border: 1px solid #96b566;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(150, 181, 102);
  border-bottom: 3px solid #96b566;
  color: #739140;
  font-size: 20px;
  font-family: 'Oswald', sans-serif !important;
  margin: 0;
  padding: 5px 20px;
  border-radius: 4px !important;
  -webkit-border-radius: 4px !important;
}
.addToCartTrademark .text {
  position: relative;
  font-family: 'Oswald', sans-serif !important;
  top: -5px;
  font-size: 21px;
  margin-right: 4px;
  padding-left: 13px;
}
.addToCartTrademark .cartIcon {
  background: url("/src/assets/images/sprites/sprite1.png") repeat scroll 0 0 transparent;
  background-position: -304px -310px;
  display: inline-block;
  width: 24px;
  height: 29px;
}
.addToCartInfo {
  text-align: right;
}
.addToCartInfo h3 {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 24px;
  font-family: 'Oswald',sans-serif !important;
  font-weight: 400;
}
.addToCartInfo h3 span {
  font-size: 26px;
  font-weight: bold;
}
.sprite-payment-system {
  background: url("/src/assets/images/sprites/sprite2.png") repeat scroll 0 0 transparent;
  background-position: -7px -573px;
  width: 353px;
  height: 27px;
  position: relative;
  top: 10px;
}
.cart-info:after {
  content: '';
  display: block;
  clear: both;
}
h2 {
  margin-top: 0;
  border-bottom: 1px dashed #dddddd;
  padding-bottom: 10px;
}
h3 {
  font-weight: 300;
